@media (min-width: 1100px) {
    .container {
        padding-left: 100px;
        padding-right: 100px;
        width: 1100px;
    }
}

.container {
    display: -webkit-flex;
    display: flex;
    min-height: 80%;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: center;
            justify-content: center;
    padding-top: 50px;
    padding-bottom: 50px;
    width: 90%;
    margin: 0 auto;
}
.click-item {
    background-size: cover;
    background-position: 50%;
    height: 60px;
    width: 65px;
    margin: 8px 5px;
    border: 5px solid #f1f1f1;
    border-radius: 3px;
    box-shadow: 0 3px 6px rgba(0,0,0,.16), 0 3px 6px rgba(0,0,0,.23);
    transition-duration: 0.4s;
    cursor: pointer;
    float: left;
}
.click-item:after{
    opacity: 0;
    transition: all 0.8s
}

.masked {
    background:url(https://i.imgur.com/ZD5hVAl.jpg);
    transition-duration: 0.4s;
}
.masked:after{
    opacity: 0;
    transition: all 0.8s
}
.guessed {
    background:rgba(0, 0, 0, 0);
    box-shadow: none;
}
